
























import { Component, Vue, Prop } from 'vue-property-decorator';
import SectionLanding from '@/components/SectionLanding.vue';
import SectionServices from '@/components/SectionServices.vue';
import SectionContact from '@/components/SectionContact.vue';
import SectionAbout from '@/components/SectionAbout.vue';
import NavBar from '@/components/NavBar.vue';
import { NavItem } from '@/models/navItem';
import SlideIn from '@/components/Animations/SlideIn.vue';

@Component({
  components: {
    SectionLanding,
    NavBar,
    SlideIn,
    SectionServices,
    SectionContact,
    SectionAbout
  }
})
export default class HomePage extends Vue {
  navItems: NavItem[] = [];

  // Necessary to prevent the first section to jump because navbar got fixed
  fixed = false;

  mounted() {
    // @ts-ignore
    this.navItems = this.$t('navigation.navItems') as NavItem[];
    this.listenForScrolling();
  }

  listenForScrolling() {
    window.addEventListener('scroll', () => {
      const sections = document.getElementsByClassName('section');
      const scrollPosition = document.documentElement.scrollTop;

      for (let i = sections.length - 1; i >= 0; i--) {
        const sectionElement = sections[i] as HTMLElement;
        if (scrollPosition >= sectionElement.offsetTop) {
          const selectedNavItem = this.navItems.find(item => item.sectionName === sectionElement.id);
          this.selectNavItem(selectedNavItem!);
          break;
        }
      }

    });
  }

  selectNavItem(navItem: NavItem) {
    this.navItems = this.navItems.map(item =>
      item === navItem ? { ...item, active: true } : { ...item, active: false }
    );
  }
}
